<template>
  <div class="hash">



        <!--  -->
        <div class="cardbox" style="margin:10px 0; padding:25px; ">
            <h2>
                
                <small>
                    <!-- <button id="loadTemplate" class="ui button small" @click="showModal = true"></button> -->
                    <button
                    id="loadTemplate"
                    class="ui button small "
                    v-bind:class="[tempLectures.length>0?'green':'']"
                    @click="modal.tempLecture=true"
                    v-if="!flag">
                          {{ tempLectures.length }}
                    </button>
                </small>
            </h2>
        </div>

        <!--  -->
        <div class="cardbox" style="margin:0; padding:25px; ">

            <table class="ui table celled form centerLayout">
                <colgroup>
                    <col width="7.5%">
                    <col width="60%">
                </colgroup>
                <tr>
                    <th></th>
                    <td>
                        <input type="text" placeholder="  " style="width:95%;" v-model="summary.lec_title"  >
                    </td>
                </tr>

                <tr>
                    <th class="borderTop"></th>
                    <td>
                        <textarea style="width:95%;" rows="6" placeholder="  " v-model="summary.lec_goal"></textarea>
                    </td>
                </tr>
                <tr>
                    <th class="borderTop"></th>
                    <td>
                        <textarea style="width:95%;" rows="6" placeholder="  " v-model="summary.lec_content"></textarea>
                    </td>
                </tr>
                <tr>
                    <th class="borderTop"></th>
                    <td>
                        <textarea style="width:95%;" rows="6" placeholder="  " v-model="summary.lec_effect"></textarea>
                    </td>
                </tr>
                <tr>
                    <th class="borderTop"></th>
                    <td>
                        <input type="text" placeholder="  " style="width:95%;" v-model="summary.lec_target">
                    </td>
                </tr>

                <tr>
                    <th class="borderTop">/</th>
                    <td>
                        <input id="file" type="file" placeholder="  " style="width:85%;" >
                        <!-- <input id="file" type="file" placeholder="  " style="width:85%;" v-model="summary.lec_introFile"> -->
                        <button type="button" for="file" class="ui basic button" style="width:10%; height:42.5px;"></button>
                    </td>
                </tr>
                <tr>
                    <th class="borderTop"></th>
                    <td>
                        <input type="date" placeholder="  " style="width:27%;" v-model="summary.lec_startDate">
                        &nbsp;&nbsp;&nbsp;
                        <input type="date" placeholder="  " style="width:27%;" v-model="summary.lec_endDate">
                    </td>
                </tr>
            </table>

        </div>

        <!--  -->
        <div class="cardbox" style="margin:10px 0; padding:25px; text-align:center;">
            <button class="ui button"> </button>
            <button class="ui button" v-on:click="save"></button>
            <button class="ui primary button" @click.prevent="goTo"></button>
        </div>
        <!-- content -->




<!-- ======================== Modal ============================ -->

    <!--   -->
    <modal v-if="showModal" @close="showModal = false" w="w-50">
        <h3 slot="header"> </h3>
        <div slot="body">
            <table class="ui celled table">
                <colgroup>
                    <col width="8%">
                    <col width="50%">
                </colgroup>
                  <thead>
                    <tr>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th></th>
                    </tr>
                  </thead>
                  <tbody v-if="tempSummary">
                      <tr>
                        <td colspan="3">  .</td>
                      </tr>

                  </tbody>
                  <tbody v-else>
                      <tr v-for="temp in tempSummary">
                        <td><input type="radio" v-bind:value="temp.lec_idx" v-model="summaryImport" ></td>
                        <td>{{temp.lec_title}}</td>
                        <td>
                            
                        </td>
                        <td>{{temp.lec_start_date}}</td>
                      </tr>
                  </tbody>
            </table>
        </div>
    </modal>



    <!--   -->
    <modal v-if="modal.tempLecture" @close="modal.tempLecture = false" w="w-60">
        <h3 slot="header"> </h3>
        <div slot="body">
            <table class="ui celled table">
                <colgroup>
                    <col width="8%">
                    <col width="50%">
                </colgroup>
                    <thead>
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody v-if="!tempLectures">
                        <tr>
                            <td colspan="3">  .</td>
                        </tr>
                    </tbody>
                    <tbody v-else>
                        <tr class="tempRow" v-for="temp in tempLectures" v-bind:class="[summaryImport==temp.lec_idx? 'tempSelect':'']" @click="chooseRow(temp.lec_idx)">
                            <td><input type="radio"   v-bind:value="temp.lec_idx" v-model="summaryImport" ></td>
                            <td>{{temp.lec_title}}</td>
                            <td>
                                
                            </td>
                            <td>{{temp.lec_startDate}}</td>
                            <td>
                                <button type="button" class="ui basic red mini button" @click.prevent="removeTempLecture(temp.lec_idx)"></button>
                            </td>
                        </tr>
                    </tbody>
            </table>
        </div>
        <div slot="footer">
            <div class="ui two bottom attached buttons">
                <div class="ui button" @click.prevent="modal.tempLecture=false"></div>
                <div class="ui blue button" @click.prevent="chooseLecture"></div>
            </div>
        </div>
    </modal>



<!-- ======================== Modal ============================ -->







  </div>
</template>



<script>
import { Modal } from '../../components'

export default {
    name: 'NewSummary',


    // ======  ====== //
    components : {
        'modal' : Modal
    },


    // ====== Data ====== //
    data () {
        return {
            flag : false, ///  
            lec_idx:'',
            tempSelect:-1, //   
            modal:{
                tempLecture: false //  
            },
            showModal: false,
            summaryImport:null, //  
            closeMsg:'',
            summary: {
                lec_title: '     - ',
                lec_content : 'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.',
                lec_goal : 'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.',
                lec_effect : 'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.',
                lec_target: '   ',
                lec_startDate : '2018-02-16',
                lec_endDate : '2018-03-22'
            }, // 
            tempSummary:{}, //  
            tempLectures:[], //   
            errors: []
        }
    },//data




    // ====== Created ====== //
    created() {

        //    
        var f = this.$ro.history.current.query.flag
        if(f){// 

            this.$set(this, 'summaryImport', f)
            this.$set(this, 'flag', true)
            this.chooseLecture() //  

        }else{// 

            const id = sessionStorage.getItem('lecture-idx')
            this.$set(this, 'lec_idx', id ? id : -1)

            //        
            let lec = sessionStorage.getItem('lecture-summary');
            if(lec){
                this.summary = JSON.parse(lec);
            }

            // DB  
            this.tempLectureLoad()
        }//else



    },



    // ======  ====== //
    methods:{

        // == ON == //
        onModal(){
            this.mactive = "active"
        }, // onModal



        // ==   == //
        tempLectureLoad() {

            this.$http.get('/api/lectures/temp')
            .then(resp=>{
                // console.log(resp.data);
                this.$set(this, 'tempLectures', resp.data.tempLectures)
            }).catch(err=>{
                alert('Error - ')
            })

        }, // tempLectureLoad






        // ==   -  == //
        chooseRow(id){
            this.$set(this, 'summaryImport', id)
        },




        // ==   == //
        chooseLecture()  {

            var id = this.summaryImport //  

            if (id==null) {
                alert('  ')
                return
            }

            // 
            this.$http.get('/api/lectures/dt/'+id)
            .then(resp=>{
                var tempLec = resp.data.lecture
                var tempKpi = resp.data.kpi
                var tempCompanies = resp.data.companies
                var tempStudents = resp.data.students
                var tempGroups = resp.data.groups
                var da = [ new Date(tempLec.lec_startDate) , new Date(tempLec.lec_endDate) ]

                //  
                tempLec.lec_startDate =
                da[0].getFullYear() + '-' +
                ( da[0].getMonth()+1 < 10 ? '0' : '' ) + (da[0].getMonth()+1) + '-' +
                ( da[0].getDate() < 10 ? '0' : '' ) + da[0].getDate()

                //  
                tempLec.lec_endDate =
                da[1].getFullYear() + '-' +
                ( da[1].getMonth()+1 < 10 ? '0' : '' ) + (da[1].getMonth()+1) + '-' +
                ( da[1].getDate() < 10 ? '0' : '' ) + da[1].getDate()

                //  
                this.$set(this, 'summary', {
                    lec_title               : tempLec.lec_title,
                    lec_goal              : tempLec.lec_goal,
                    lec_content        : tempLec.lec_content,
                    lec_effect           : tempLec.lec_effect,
                    lec_startDate     : tempLec.lec_startDate,
                    lec_endDate      : tempLec.lec_endDate
                })

                this.$set(this, 'lec_idx', id)
                sessionStorage.setItem('lecture-idx', id)

                //   
                // # 
                if(tempLec.sessions.length>0){
                    sessionStorage.setItem('lecture-term', JSON.stringify({ sessionDetail:tempLec.sessions }))
                    sessionStorage.setItem('lecture-sessionCount', tempLec.sessions.length)
                }
                // 
                if(tempLec.timetables){
                    sessionStorage.setItem('lecture-timetables', JSON.stringify(tempLec.timetables))
                }
                // KPI
                if(tempKpi){
                    sessionStorage.setItem('lecture-kpi', JSON.stringify(tempKpi))
                }
                // 
                if(tempCompanies){
                    sessionStorage.setItem('lecture-companies', JSON.stringify(tempCompanies))
                }
                // 
                if(tempStudents){
                    sessionStorage.setItem('lecture-students', JSON.stringify(tempStudents))
                }
                // 
                sessionStorage.setItem('lecture-teamBuilding', JSON.stringify({teams : tempGroups, students : tempStudents}))
            })
            .catch(err=>{
                console.log(err);
                alert('Error')
            })
            this.modal.tempLecture=false

        }, // chooseLecture








        // ==   == //
        removeTempLecture(id){
            if( confirm('   .   ?') ){
                var rid = this.tempLectures.findIndex((t)=>{
                    return t.lec_idx == id
                })

                this.$http.delete('/api/lectures/'+id)
                .then(resp=>{
                    //     
                    if(this.tempLectures.length>0)
                        this.tempLectures.splice(rid, 1)
                    else
                        this.tempLectures.pop()

                    // Modal OFF
                    this.$set(this.modal , 'tempLecture', false)
                })
                .catch(err=>{
                    alert('Error ')
                })
            }// if
        },// removeTempLecture()












        // ==  == //
        save() {
            // TEST
            // console.log("this.lec_idx : ", this.lec_idx);
            // console.log(this.summary);
            // return

            if (this.lec_idx < 0) {
                //   - 
                this.$http.post('/api/lectures/create/summary', this.summary)
                .then(resp=>{

                    this.$set(this, 'lec_idx', resp.data.lec_idx)
                    sessionStorage.setItem('lecture-idx', resp.data.lec_idx)

                    // 
                    sessionStorage.setItem('lecture-summary', JSON.stringify(this.summary));
                    alert('.')
                })
                .catch(err=>{
                    console.log(err)
                    alert('Error - insert')
                    return
                })

            }else{
                //   - 
                this.$set(this.summary, 'lec_idx', this.lec_idx) //  
                this.$http.put('/api/lectures/create/summary', this.summary)
                .then(resp=>{
                    // 
                    sessionStorage.setItem('lecture-summary', JSON.stringify(this.summary));
                    sessionStorage.setItem('lecture-idx', this.lec_idx)
                    alert('.')
                })
                .catch(err=>{
                    console.log(err)
                    alert('Error - update')
                    return
                })

            }// else


        }, // save


        // ==   == //
        goTo(){
            if (!sessionStorage.getItem('lecture-summary')) {
                alert('   .')
                return
            }
            this.$ro.push({ path: '/new/aplterm' })
        }// goTo

    } // methods
}
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

        a{
            cursor: pointer;
        }
        th{
            background: #F9FAFB;
            padding:0.92857143em 0.78571429em;
        }
        .borderBottom{
            border-bottom: 1px solid rgba(34, 36, 38, 0.1);
        }
        .borderTop{
            border-top: 1px solid rgba(34, 36, 38, 0.1);
        }


        .tempRow{ cursor: pointer; }
        .tempRow:hover{ background: #f9f9f9 !important; }

        .tempSelect{
            background: #f9f9f9 !important;
        }



</style>



