<template>
    <div class="">

        <!-- ========================   ============================ -->
        <div class="cardbox" style="margin:10px 0; padding:25px; ">
            <h2>
                APL
                <button class="ui icon button tiny" style="margin: 4px 0 0 5px; padding:7px; font-size:.5em; position:absolute;">
                    <i class="help circle icon"></i> 
                </button>
            </h2>
        </div>
        <!-- ========================   ============================ -->










        <!-- ========================  ============================ -->
        <div class="cardbox" style="margin:0; padding:25px; ">
            <h3> </h3>
            <table class="ui table celled form centerLayout">
                <colgroup>
                    <col width="40%">
                    <col width="40%">
                    <col width="20%">
                </colgroup>
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                <tr>
                    <td>{{ date.startDate }}</td>
                    <td>{{ date.endDate }}</td>
                    <td>
                        <input type="number" value="0" style="width:55%;" v-model="cc" min="0" max="100" step="1">
                        <button type="button" class="ui primary button" @click.prevent="addSession"></button>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
        <!-- ========================  ============================ -->










        <!-- ========================  ============================ -->
        <div class="cardbox" style="margin:10px 0; padding:25px; min-height:350px;">

            <h3> </h3>
            <p>*   <b>APL</b>  .</p>
            <table class="ui celled table form">
                <colgroup>
                    <col width="7%">
                    <col width="17%">
                    <col width="17%">
                    <!-- <col width="53%"> -->
                </colgroup>
                <thead>
                    <tr>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th></th>
                    </tr>
                </thead>
                <tbody  v-if="terms.sessionCount<1">
                    <tr>
                      <td colspan="3">   .   </td>
                    </tr>
                </tbody>
                <tbody v-else>
                    <tr v-for="(t, i) in terms.sessionDetail">
                      <td>
                          {{ i+1 }}
                      </td>
                      <td >
                          <div class="ui input disabled" v-if="i==0?'disabled':''">
                                  <input type="date" placeholder="  " style="width:95%;" v-bind:value="terms.sessionDetail[i].ls_startDate" >
                          </div>
                          <div class="ui input" v-else>
                              <input type="date" placeholder="  " style="width:95%;" v-model="terms.sessionDetail[i].ls_startDate" @change="dateRagneCheck(i)">
                          </div>
                      </td>
                      <td>
                          <div class="ui input disabled" >
                              {{ terms.sessionDetail[i].ls_endDate }}
                          </div>
                      </td>
                      <td>
                          <input type="text" placeholder="  " style="width:100%;" v-model="terms.sessionDetail[i].ls_location">
                      </td>
                    </tr>
                </tbody>
            </table>

        </div>
        <!-- ========================  ============================ -->








        <!-- ========================  ============================ -->
        <div class="cardbox" style="margin:10px 0; padding:25px; text-align:center;">
            <button class="ui button  button" @click.prevent="save"></button>
            <button class="ui primary button" @click.prevent="goTo"></button>
        </div>
        <!-- ========================  ============================ -->


    </div>
</template>



<!-- Script -->
<script>
const page = 'NewAplterm';

export default {
    name: page,
    data () {
        return {
            deleteFlag         : false, //   (true: )
            ls_idx        : 0, //   
            lec_idx               : '', // 
            showModal       : false,
            msg                   : page,
            cc                       : 0, // 
            termCount        : 0,

            date : {
                startDate:'', endDate:''
            },
            terms : { // apl model
                sessionCount:0,
                sessionDetail: []
            },

            sd: []
        }
    }, // data



    created () {
        const id = sessionStorage.getItem('lecture-idx')
        let summary = JSON.parse(sessionStorage.getItem('lecture-summary'));

        this.$set(this, 'lec_idx', id ? id : -1)
        console.log("this.lec_idx : ",this.lec_idx);

        if (summary) {
            this.$set(this.date, 'startDate', summary.lec_startDate)
            this.$set(this.date, 'endDate', summary.lec_endDate)
        }

        let term = sessionStorage.getItem('lecture-term');
        let count = sessionStorage.getItem('lecture-sessionCount');



        if(term){
            this.$set(this, 'terms', JSON.parse(term))
            // console.log(this.terms);
            this.cc = this.terms.sessionCount = count;

            //    
            if(this.terms.sessionDetail.length>0){
                this.fixedDate()
            }

        }// if



    }, // created








    methods: {

        // ==  == //
        fixedDate(){
            var L = this.terms.sessionDetail.length-1 < 0 ? 0 : this.terms.sessionDetail.length-1
            // console.log('LLLL: ', L);
            this.$set( this.terms.sessionDetail[L], 'ls_endDate', this.date.endDate) //  
            this.$set( this.terms.sessionDetail[0], 'ls_startDate', this.date.startDate) //  
        },



        // ==  == //
        dateRagneCheck( addr ){
            /*
               
            /       
            */
            // console.log(this.terms.sessionDetail[addr]);
            var dayOne = 24*60*60 //  
            var selectDate      = new Date(this.terms.sessionDetail[addr].ls_startDate).getTime() / 1000           //  
            var selectDate2    = new Date(this.terms.sessionDetail[addr].ls_endDate).getTime() / 1000            //  - 
            var startDate        = new Date(this.terms.sessionDetail[0].ls_startDate).getTime() / 1000                // 
            var endDate         = new Date(this.terms.sessionDetail[this.cc-1].ls_endDate).getTime() / 1000      // 
            var beforeDate    = new Date(this.terms.sessionDetail[addr-1].ls_startDate).getTime() / 1000         //  
            var beforeDate2  = new Date(this.terms.sessionDetail[addr-1].ls_endDate).getTime() / 1000         //  


            if( ( selectDate <= startDate+dayOne ) || ( selectDate >= endDate ) ){
                this.$set(this.terms.sessionDetail[addr], 'ls_startDate', '')
                this.$set(this.terms.sessionDetail[addr-1], 'ls_endDate', '--')
                alert('    .')
                return
            }// if


            //    
            var aaaa = selectDate-dayOne //  
            var val = new Date( aaaa*1000 ) // 

            var intMonth = Number(val.getMonth()) +1 //  Month
            var vYear = val.getFullYear()
            var vMonth = ( intMonth < 10 ? '0' : '') + intMonth  // getMonth -1 
            var vDay = val.getDate()

            val  =  vYear+ "-" + vMonth + "-" + vDay
            this.$set(this.terms.sessionDetail[addr-1], 'ls_endDate', val)

        }, // dateRagneCheck



        // == () == //
        addSession(){//
            /*
            ()       insert
                   /
            */


            // 
            if(this.cc > this.terms.sessionDetail.length)
                this.$set(this, 'deleteFlag', false)
            else
                this.$set(this, 'deleteFlag', true)


            this.$set(this.terms, 'sessionCount', this.cc)
            this.terms.sessionDetail = []

            var tempData;
            for(var ii=0;   ii<this.cc;  ii++){
                tempData = {
                    id: ii,
                    ls_startDate:'0000-00-00', //
                    ls_endDate:'0000-00-00', // 
                    ls_startTime:'', //
                    ls_endTime: '', // 
                    sessions:new Array(),
                    ls_idx: this.ls_idx+ii,
                    ls_seq: ii+1,
                }
                this.terms.sessionDetail.push(tempData)
            }// for

            this.fixedDate()
        }, //



        // ==  == //
        save(){ //
            // console.log("this.terms.sessionDetail[0].ls_idx : ");
            // console.log("this.terms.sessionCount: ",this.terms.sessionCount);
            // console.log(this.terms.sessionDetail);
            // return

            if (this.terms.sessionDetail==''  ||  this.terms.sessionDetail==null  ||  this.terms.sessionDetail==undefined) {
                alert('  ')
                return
            }

            //     
            if (sessionStorage.getItem('lecture-timetables')) {
                if ( !confirm('      . APL ?') )
                    return
                sessionStorage.removeItem('lecture-timetables');
            }// if


            //  
            var id = this.lec_idx


            //    
            if (id<0 || id==undefined) {

                if ( !confirm('  .  ?') )
                    return

                // HTTP
                this.$http.post('/api/lectures/create/aplterm', this.terms)
                .then(resp=>{
                    // console.log(resp)
                    var sid = resp.data.session_idx //  ID
                    this.$set(this, 'ls_idx', sid) //  
                    for(var t in this.terms.sessionDetail){
                        this.$set(this.terms.sessionDetail[t], 'ls_idx', sid++)
                    }// for
                })
                .catch(err=>{
                    console.log(err)
                    alert('Error')
                    return
                })

            }else{
                //   
                this.$http.post('/api/lectures/create/aplterm', { lec_idx:id, terms: this.terms})
                .then(resp=>{
                    console.log(resp);
                    var sid = resp.data.session_idx //  ID
                    this.$set(this, 'ls_idx', sid) //  
                    for(var t in this.terms.sessionDetail){
                        this.$set(this.terms.sessionDetail[t], 'ls_idx', sid++)
                    }// for

                    // 
                    sessionStorage.setItem('lecture-term', JSON.stringify(this.terms));
                    sessionStorage.setItem('lecture-sessionCount', this.terms.sessionCount);
                    alert('.')
                })
                .catch(err=>{
                    console.log(err)
                    alert('Error')
                    return
                })

            }// else




        }, // 



        // ==  == //
        goTo(){
            if ( !sessionStorage.getItem('lecture-term') ) {
                alert('   .')
                return
            }
            this.$ro.push({ path: '/new/timetable' })
        }// goTo

    } // methods
}
</script>



<!-- Style -->
<style scoped>
    a {
        color: #42b983;
    }

    .disabled {
        cursor: not-allowed !important;
    }
</style>



