<script>
import { Modal, SearchForm, CardTitle } from '../../components'
const page = 'NewComplate';

export default {
    name: page,

    // ======  ====== //
    components: {
        'modal' : Modal,
        'search-form' : SearchForm,
        'card-title' : CardTitle
    },

    data () {
        return {
            lec_idx:0, // lecture id
            msg: page,
            modal:{
                // agree : true
                agree : false
            },
            term:false //  
        }
    }, // data









    created() {
        const id = sessionStorage.getItem('lecture-idx')
        this.$set(this, 'lec_idx', id ? id : -1)

        // this.$set(this.modal, 'agree', (this.term?false:true))

        this.$http.get('/api/users/session')
        .then(resp=>{
            console.log(resp);
        })
    }, // created


    methods: {

        // 
        agree() {
            this.$set(this, 'term', true)
            this.$set(this.modal, 'agree', false)
        }, // agree

        deagree() {
            this.$set(this, 'term', false)
            this.$set(this.modal, 'agree', false)
        },

        // 
        complete() {

            //  
            var lec=[
                sessionStorage.getItem('lecture-summary'),
                sessionStorage.getItem('lecture-term'),
                sessionStorage.getItem('lecture-timetables'),
                sessionStorage.getItem('lecture-companies'),
                sessionStorage.getItem('lecture-teamBuilding'),
                sessionStorage.getItem('lecture-students'),
            ]


            var conf = false //  
            var keys = Object.keys(lec)

            for(var ii  in  keys){
                if (lec[ii] == null  ||  lec[ii]==undefined) {
                    conf=true
                }// if
            }// for

            if (conf) {
                 alert('   ')
                 this.$set(this, 'term', false)
                    return
            }// if



            this.$http.post('/api/lectures/complete', {
                lec_idx: this.lec_idx
            })
            .then(resp=>{
                console.log(resp);
                alert(' .     .')
                this.$ro.push({path:'/lectures/wait'})
            }).catch(err=>{
                alert("  .")
                console.log(err);
            })// $http

        } // complete()



    }// methods
}
</script>



