<template>
  <div class="hash">


      <h1 class="ui header">{{ company.com_name }}</h1>

        <table class="ui celled table">
        <colgroup>
            <col width="13%;">
            <col width="42%;">

            <col width="13%;">
            <col width="32%;">
        </colgroup>
        <tbody>
            <tr>
                <td></td>
                <td>{{ company.com_code }}</td>
                <td></td>
                <td>{{ company.com_category }}</td>
            </tr>
            <tr>
                <td></td>
                <td>{{ company.com_location }}</td>
                <td></td>
                <td>{{ lectures.length }}</td>
            </tr>
            <tr>
                <td> </td>
                <td>{{ company.com_registrationCount }}</td>
                <td> </td>
                <td>{{cumulativeCount}}</td>
            </tr>
        </tbody>
    </table>




    <br>

    <h4>{{ company.com_name }}  </h4>
    <table class="ui table celled">
        <thead>
            <tr>
                <th>no</th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
            </tr>
        </thead>
        <tbody v-if="lectures.length==0">
            <tr>
                <td colspan="6">     .</td>
            </tr>
        </tbody>
        <tbody v-else>
            <tr v-for="(lec, lid) in  lectures">
                <td>{{ lid+1 }}</td>
                <td>{{ lec.lec_title }}</td>
                <td>{{ lec.lec_startDate }} ~ {{ lec.lec_endDate }}</td>
                <td>-</td>
                <td>{{ lec.lec_studentCount }}</td>
                <td>{{ lec.mg_name }}</td>
            </tr>
        </tbody>
    </table>



    <br>
    <div class="ui two column centered grid">
      <div class="ui centered pagination menu">
              <a class="icon item">
                <i class="left chevron icon"></i>
              </a>
              <a class="item">1</a>
              <a class="item">2</a>
              <a class="icon item">
                <i class="right chevron icon"></i>
              </a>
            </div>

    </div>








  </div>
</template>

<script>
const page = "CompanyDetail"
export default {
    name: page,

    // data
    data () {
        return {
            msg: page,
            com_code:'', // 
            company:{}, // 
            lectures:[], //   
            cumulativeCount : 0
        }
    }, // data


    //Created
    created(){
        //  
        var id = this.$ro.history.current.params.id
        this.$set(this, 'com_code', id)
        this.getCompany(id) //  
    },


    //methods
    methods: {

        //  
        getCompany(cid){
            this.$http.get('/api/companies/'+cid)
            .then(resp=>{
                console.log("LELE : ", resp.data.lectures.length);
                this.$set(this, 'company', resp.data.company)
                this.$set(this, 'lectures', resp.data.lectures)
                //  
                resp.data.lectures.forEach((r)=>{
                    if(cid==r.com_code)
                        this.$set(this, 'cumulativeCount', this.cumulativeCount+r.lec_studentCount)
                })
            })
            .catch(err=>{
                alert('Error - ')
            })
        }//  

    }// methods
}
</script>


<!--  -->
<style scoped>

</style>



