                                </td>
                            </tr>
                        </tbody>
                        <tbody v-else>
                            <tr v-for="(team, ii) in teams" v-bind:class="[colorAnimate==ii ? 'color-change-2x':'']">
                                <td >{{ team.group_name }}</td>
                                <td class="center aligned">{{ (team.students==undefined? '0' : team.students.length+'') }}</td>
                                <td class="center aligned" style="padding-right:0; padding-left:5px;">
                                    <button type="button" class="tiny basic ui button teamEdit" @click.prevent="editTeam(team.group_idx)"></button>
                                    <button type="button" class="tiny basic red ui button teamEdit" @click.prevent="removeTeam(team.group_idx)"></button>
                                </td>
                                <td class="center aligned"><input type="radio" name="" v-bind:value="team.group_idx" v-model="selectTeam"></td>
                            </tr>
                            <tr>
                                <td colspan="4" class="center aligned">
                                    <!--     -->
                                    <button type="button" class="ui blue button tiny basic" @click.prevent="modal.createTeam=true"></button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <!-- <p>{{this.checkedStudents}}</p> -->
            </div>



            <!--  -->
            <div class="two wide column" style="text-align:center; padding:20% 2%;">
                <button type="button" class="ui blue basic button small" @click.prevent="addStudents">
                    <i class="arrow left icon"></i> 
                </button>
                    <br><br>
                <button type="button" class="ui blue basic button small">
                     <i class="arrow right icon"></i>
                </button>
                    <br><br><br>

                    <button type="button" class="ui blue basic button small" @click="modal.random=true">
                        
                    </button>
            </div>



            <!--  -->
            <div class="seven wide column" style=" padding:0; background:#f7f7f7;">
                <h3 style="background:#fff; padding:5px;">
                    
                    <small style="color:orange;">[ {{ students.length }} ]</small>
                </h3>
                <div class="" style="height:600px; overflow:auto; padding:0 .5rem;">
                    <table class="ui celled table">
                        <colgroup>
                            <col width="5%">
                            <col width="20%">
                            <col width="25%">
                            <col >
                            <col >
                        </colgroup>
                        <thead>
                            <tr>
                                <th class="center aligned"></th>
                                <th class="center aligned"></th>
                                <th class="center aligned"></th>
                                <th class="center aligned"></th>
                                <th class="center aligned"></th>
                            </tr>
                        </thead>
                        <tbody>
                            <!-- selectable positive -->
                            <tr v-for="(std, i) in students" :key="i" class="trhover" v-bind:class="[]" >
                                <label for="i" v-bind:for="i">
                                <td class="center aligned">
                                    <input type="checkbox" v-bind:id="i" v-bind:name="std" v-bind:value="std" v-model="checkedStudents">
                                </td>
                                </label>
                                <td class="center aligned"><label for="i" v-bind:for="i">{{ std.stu_name }}</label></td>
                                <td class="center aligned">{{ std.stu_company }}</td>
                                <td class="center aligned">{{ std.stu_department }}</td>
                                <td class="center aligned">{{ std.stu_position }}</td>
                            </tr>

                        </tbody>
                    </table>
                </div>
                <!-- <br>
                <div class="ui two column centered grid">
                  <div class="ui centered pagination menu">
                          <a class="icon item">
                            <i class="left chevron icon"></i>
                          </a>
                          <a class="item">1</a>
                          <a class="item">2</a>
                          <a class="item">3</a>
                          <a class="item">4</a>
                          <a class="icon item">
                            <i class="right chevron icon"></i>
                          </a>
                        </div>
                </div> -->
            </div>



                <br>
                <br>



            </div>

            <div class="cardbox" style="margin:10px 0; padding:25px; text-align:center;">
                <button class="ui button" @click.prevent="resetSessionStorage"></button>
                <button class="ui button" @click.prevent="save"></button>
                <button class="ui primary button" @click.prevent="goTo"></button>
            </div>








<!-- ======================== Modal ============================ -->

    <!--   -->
    <modal v-if="modal.createTeam" @close="modal.createTeam = false" w="w-40" h="">
        <h3 slot="header"></h3>
        <div slot="body" class="ui form">

            <table width="100%">
                <colgroup>
                    <col width="10%">
                </colgroup>
                <tr>
                    <td></td>
                    <td class="field">
                          <input type="text" placeholder="     ." v-model="teamInfo.group_name">
                    </td>
                </tr>
                <tr>
                    <td></td>
                    <td class="field">
                          <textarea rows="2" v-model="teamInfo.description"></textarea>
                    </td>
                </tr>
            </table>

        </div>
        <div slot="footer">
            <div class="ui two bottom attached buttons">
                <div class="ui button" @click.prevent="modal.createTeam=false"></div>
                <div class="ui button blue" @click.prevent="addTeam"></div>
            </div>
        </div>
    </modal>






    <!--      -->
    <modal v-if="modal.editTeam" @close="modal.editTeam = false" w="w-50" h="h-50">
        <h3 slot="header"> </h3>
        <div slot="body" class="ui form">

            <table width="100%">
                <colgroup>
                    <col width="10%">
                </colgroup>
                <tr>
                    <td></td>
                    <td class="field">
                          <input type="text" placeholder="" v-model="teams[selectTeam].name">
                    </td>
                </tr>
                <tr>
                    <td></td>
                    <td class="field">
                          <textarea rows="2" v-model="teams[selectTeam].description"></textarea>
                    </td>
                </tr>
            </table>

            <br>
            <br>


            <div class="seven wide column" style=" padding:0; background:#f7f7f7;">
                <h3 style="background:#fff; padding:5px;">
                    
                    <small style="color:orange;">[ {{ teams[selectTeam].students==undefined ? '0': teams[selectTeam].students.length }} ]</small>
                </h3>
                <div class="" style=" overflow:auto; padding:0 .5rem;">
                    <table class="ui celled table">
                        <colgroup>
                            <col width="20%">
                            <col width="25%">
                            <col >
                            <col >
                            <col width="12%">
                        </colgroup>
                        <thead>
                            <tr>
                                <th class="center aligned"></th>
                                <th class="center aligned"></th>
                                <th class="center aligned"></th>
                                <th class="center aligned"></th>
                                <th class="center aligned"></th>
                            </tr>
                        </thead>
                        <tbody v-if="teams[selectTeam].students==undefined">
                            <!-- selectable positive -->
                            <tr>
                                <td class="center aligned" colspan="5">   .</td>
                            </tr>
                        </tbody>
                        <tbody v-else>
                            <tr v-for="(std, i) in teams[selectTeam].students" :key="i">
                                <td class="center aligned"><label for="i" v-bind:for="i">{{ std.stu_name }}</label></td>
                                <td class="center aligned">{{ std.stu_company }}</td>
                                <td class="center aligned">{{ std.stu_department }}</td>
                                <td class="center aligned">{{ std.stu_position }}</td>
                                <td class="center aligned">
                                    <button type="button" class="ui red basic button tiny" @click.prevent="removeStudent(std.id)"></button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>



        </div>
        <div slot="footer">
            <div class="ui two bottom attached buttons">
                <div class="ui blue button" @click.prevent="modal.editTeam=false"></div>
            </div>
        </div>
    </modal>







    <!--   -->
    <modal v-if="modal.random" @close="modal.editTeam = false" w="w-50" h="h-50">
        <h3 slot="header"></h3>
        <div slot="body" class="ui form">





        </div>
        <div slot="footer">
            <div class="ui two bottom attached buttons">
                <div class="ui button" @click.prevent="modal.random=false"></div>
            </div>
        </div>
    </modal>


<!-- ======================== Modal ============================ -->




    </div>
</template>



<!-- Script -->
<script>
import { Modal, SearchForm, CardTitle } from '../../components'
const page = 'NewTeam';

export default {
    name: page,

    // ======  ====== //
    components: {
        'modal' : Modal,
        'search-form' : SearchForm,
        'card-title' : CardTitle
    },
    // ======  ====== //





    // ============ data ============ //
    data () {
        return {
            lec_idx:0,
            msg: page,
            modal:{ // 
                createTeam : false,
                editTeam: false,
                random: false
            },
            colorAnimate: -1,
            teamInfo:{ // 
                group_idx: '', group_name: '', group_text: ''
            },
            teams:[], // 
            selectTeam: -1, //  
            selectStudent:-1, //  
            checkedStudents:[], //  
            tempTeam:{},
            arrCount:0,
            companies:[],
            students:[], //  
            students_temp:[] // 
        }
    }, // data

    watch:{
        checkedStudents(h){
            // console.log(h);
        },
        selectTeam(h){
            // console.log(h);
        }
    },
    // ============ data ============ //





    // ============ created ============ //
    created() {
        const id = sessionStorage.getItem('lecture-idx')
        this.$set(this, 'lec_idx', id ? id : 0)

        // 
        var storageData = JSON.parse(sessionStorage.getItem('lecture-teamBuilding'))
        console.log(storageData);
        var teams =( storageData==null) ? [] : storageData.teams
        var stds = JSON.parse(sessionStorage.getItem('lecture-students')) // 


        //    ->  
        if(teams.length>0){
            var stdsKeys = Object.keys(stds)
            var teamsKeys = Object.keys(teams)

            //   
            for(var ii  in  stdsKeys){
                for(var jj  in  teamsKeys){
                    //   
                    if( typeof(teams[jj].students) != 'object' ){
                        teams[jj].students = []
                    }
                    if (stds[ii].group_idx === teams[jj].group_idx) {
                            teams[jj].students.push(stds[ii])
                    }
                }// for
            }// for
            console.log(teams);
            this.$set(this, 'teams', teams)
            this.$set(this, 'students', stds)
            this.$set(this, 'students_temp', stds)
            return

        }
        else if( stds!=null && stds.length>0){
            this.$set(this, 'students', stds)
            console.log('!!!!!!!!!!');
            return
        }

        //


    }, // created
    // ============ created ============ //





    // ============ methods ============ //
    methods: {


        // ===  === //
        addTeam() {
            // ID 
            var newId = !this.teams.length ? 1 : this.teams[this.teams.length - 1].group_idx + 1;

            if (this.teamInfo.group_name == ''  ||  this.teamInfo.group_name == undefined) {
                if ( !confirm('  ?') ) {
                    return
                }
                this.teamInfo.group_name = 'A'+newId+''
            } // if


            this.teamInfo.group_idx = newId
            this.teamInfo.students = []

            //  
            this.teams.push(this.teamInfo)

            //  
            this.teamInfo = {
                group_idx: '',
                group_name: '',
                group_text: ''
            }
            this.modal.createTeam = false
        }, // addTeam




        // ===  === //
        removeTeam(id) {
            var did; // Delete Idx
            var stds; // Students

            if (!id) return

            //    
            did = this.teams.findIndex(function (team) {
                return team.group_idx === id
            })


            if (did === -1) return

            if ( confirm('      . ?') ) {

                if (this.teams[did].students != undefined || this.teams[did].students != '') {
                    // 
                    if(this.teams[did].students != '' && this.teams[did].students != undefined){
                        stds = this.students.concat(this.teams[did].students)
                        // this.students = stds;
                        this.$set(this, 'students', stds)
                    }
                }// if
                this.teams.splice(did, 1);

            }// if - confirm

        }, // removeTeam




        // ===  === //
        editTeam(id){
            //   
            var eid = this.teams.findIndex(function (team) {
                return team.group_idx === id
            })

            // this.$set(this, 'tempTeam', this.teams[eid])
            this.selectTeam = eid
            this.modal.editTeam = true
        },// editTeam






        // ===  to   === //
        addStudents(){
            var idx = this.selectTeam
            if ( idx<0  ||  idx==undefined ) {
                alert('  .')
                return
            }//if

            // 
            this.$set(this, 'selectTeam', -1)

            // 
            this.$set(this, 'colorAnimate', idx-1)


            //   
            var rid = this.teams.findIndex(function (team) {
                return team.group_idx === idx
            })

            //  
            var stds = this.checkedStudents;


            //    concat    
            if ( typeof(this.teams[rid].students) != undefined ) {
                stds = stds.concat(this.teams[rid].students)
                // console.log(this.checkedStudents);
            }
            this.$set(this.teams[rid], 'students', stds) //  

            // console.log(stds);

            //   &    =>     
            this.checkedStudents.forEach((cstd,i)=>{
                this.students.forEach((std, j)=>{
                    if (cstd.com_code==std.com_code && cstd.stu_idx==std.stu_idx) {
                        this.students.splice(j,1)
                    }
                    if (this.checkedStudents.length-1 == i && this.students.length-1==j) {
                        this.checkedStudents = [];
                    }
                })
            })

            //        ! = >   /  null pop
            var  ll = this.teams[rid].students.length;
            if (this.teams[rid].students[ll-1] == null) {
                this.teams[rid].students.pop()
            }
            // console.log(this.teams[rid]);


        }, // addStudents





        // ===  === //
        removeStudent(id) {
            var selectTeam = this.selectTeam
            var rid = this.teams[selectTeam].students.findIndex(std=>{
                return std.stu_idx == id
            })
            this.students.push(this.teams[selectTeam].students[rid])
            this.teams[selectTeam].students.splice(rid, 1)

        },




        // ===   === //
        resetSessionStorage() {
            sessionStorage.clear('lecture-teamBuilding');
        },





        // ===  === //
        save() {
            // console.log(this.teams)
            // return

            if (this.teams == undefined || this.teams.length==0) {
                if(!confirm('    ?'))
                    return
            }

            // 
            this.$http.post('/api/lectures/create/group', {
                teams  : this.teams,
                lec_idx : this.lec_idx
            })
            .then(resp=>{
                //       
                var rid = resp.data.insertIdx
                if(rid != undefined){
                    for(var ii  in  this.teams){
                        this.teams.group_idx = rid
                        rid++
                    }// for
                }
            })
            .catch(err=>{
                alert('Err- ')
            })

            sessionStorage.setItem('lecture-teamBuilding', JSON.stringify({teams:this.teams, students:this.students}))
            alert('  .')
        },// save



        goTo() {
            if (!sessionStorage.getItem('lecture-teamBuilding')) {
                alert('   .')
                return
            }
            this.$ro.push({ path:'/new/complete' })
        }// goTo



    } // ============ methods ============ //
}
</script>



<!-- Style -->
<style scoped>
    a {
        color: #42b983;
    }

    .form  input,
    .form  textarea{
        width: 100% !important;
    }

    .teamEdit{
        padding: 5px 11px;
        margin:0;
    }


    .trhover:hover{
        background: #f3f3f3;
        cursor:pointer
    }


    .color-change-2x{animation:color-change-2x 1s alternate both}
    @keyframes color-change-2x{0%{background:#E4F7BA}100%{background:none}}


</style>



