<script>
import {
    Modal,
    SearchForm,
    CardTitle,
    Timeline ,
    Feed ,
    NoContents,
    Rating,
    Comment,
    Loading
} from '../../components'

import LectureModule from './LectureModule'

export default {
    name: 'LectureDetail',

    components : {
        'modal'             : Modal,
        'search-form'   : SearchForm,
        'timeline'          : Timeline,
        'feed'                : Feed,
        'no-contents'    : NoContents,
        'rating'              : Rating,
        'comment'        : Comment,
        'lecture-module': LectureModule,
        'loading' : Loading
    },

    data () {
        return {
            modal: {
                aadd:false, // 
                actionPlan : false, // 
                addActionPlan : false, //  
                lectureComments : false, // 
            },
          msg: '     - TEST',
          lec_idx:null,
          lecture:{},
          companies: [],
          groups:[],
          students: [],
          tempStudents:[] ,  //   
          kpi:[],
          sessionTab:-1,
          sessionTab2:-1, //    
          groupTab:'' , //  
          chooseTeam:{}, //   
          companyTab:0,
          tab:3, //   
          attendanceCount : 0, //  
          avgAttendancePercent : 0, // 


          // ===  === //
          accordion : -1 , //  
          comments: [], //   
          // ===  === //




          // ===  === //
          actionplanSessionTab : -1, // 
          selectedStudent:-1, //  
          plans : [], //   
          plan : {}, //   
          // ===  === //

        }
    },// data



    // Filter
    filters: {
      capitalize: function (value) {
        if (!value) return ''
        value = value.toString()
        return value.charAt(0).toUpperCase() + value.slice(1)
      }
    },




    created(){
        var id = this.$ro.history.current.params.id
        // this.$set(this, 'lec_idx', id)
        this.$set(this, 'lec_idx', 5)  //   

        this.getLecture(id) // 
        this.getComments('feedback', id) // 

        // $On
        this.$EventBus.$on('addTeam', this.addTeam)
    },






    methods: {

        chooseGroup(id){
            if(id != -1){
                var sid = this.groups.findIndex((g)=>{
                    return g.group_idx==id
                })
                this.$set(this, 'tempStudents', this.groups[sid])
            }// if
            this.$set(this, 'groupTab', id)

        },

        // 
        selectTab(s) {
            this.tab=s;
        },

        sessionTabChange(jj){
            this.$set(this, 'sessionTab', jj)
        },

        sessionTabChange2(jj){
            this.$set(this, 'sessionTab2', jj)
        },

        getLecture(id){
            this.$http.get('/api/lectures/dt/'+id)
            .then(resp=>{
                // console.log(resp.data);
                this.$set(this, 'lecture', resp.data.lecture)
                this.$set(this, 'companies', resp.data.companies)
                this.$set(this, 'groups', resp.data.groups)
                this.$set(this, 'students', resp.data.students)
                this.$set(this, 'kpi', resp.data.kpi)
            }).catch(err=>{
                // console.log(err);
                alert('Error')
            })
        }, // getLecture





        //    
        attendancePercent(sid){
            var stds = this.students
            var all = stds.length

            var temp
            var sum=0
            for(var ii  in  stds){
                temp = JSON.parse(stds[ii].stu_attendance)
                sum+=temp[sid] ? 1 : 0
            }
            var ff = (sum/all) * 100

            //  0  X
            if (ff==0 || ff==100.0) {
                return ff.toFixed(0)
            }else{
                return ff.toFixed(1)
            }//else

        }, // attendancePercent




        //  
        attendancePercentAvg(){
            var sum=0,avg=0; // , 
            var leng = this.lecture.sessions.length //  
            var keys = Object.keys(this.lecture.sessions) // 

            for(var i=0;  i<keys.length; i++){
                sum += Number(this.lecture.sessions[i].apa)
            }

            avg = sum/leng
            if (avg==0 || avg==100.0) {
                return avg.toFixed(0)
            }else{
                return avg.toFixed(1)
            }//else
        }, // attendancePercentAvg





        // 
        attendanceCheck(stdId, atdId){
            /*
            stdId :  
            atdId :   
            */

            // var ar = this.students.findIndex((std)=>{
            //     return std.stu_idx == stdId
            // })



            var attd = JSON.parse(this.students[stdId].stu_attendance) //  
            attd[atdId] = attd[atdId]==1 ? 0 : 1


            // 
            this.$http.put('/api/students/attendance', {
                lec_idx : this.lec_idx,
                stu_idx : this.students[stdId].stu_idx,
                attendance : attd
            })
            .then(resp=>{
                this.students[stdId].stu_attendance = JSON.stringify(attd)
            })
            .catch(err=>{
                console.log("");
                console.log(err);
                alert("Error - ")
            })

        }, // 





        //  
        addTeam(){
            alert(1)
        }, // addTeam()




        // 
        removeLecture(){
            if ( !confirm('   . ?') )
                return


            this.$http.delete('/api/lectures/'+this.lec_idx)
            .then(resp=>{
                alert(' .     .')
                this.$ro.push({ path:'/lectures' })
            })
            .catch(err=>{
                alert('Error - r')
            })
        },// removeLecture()








        // ==========  /  /  ========== //


        // ===   === //
        getComments( table , lec_idx ){
            /*
            table       :   [ 'feedback' , 'action_check' ]
            lec_idx    :  
            */
            var url = '/api/comments/'+table+'/'+lec_idx
            this.$http.get(url)
            .then (resp =>{
                console.log(resp.data.comments);
                this.$set(this, 'comments', resp.data.comments)
            })
            .catch (err => {

            })
        },


        // ===   === //
        accordionOpen(lm_idx){
            this.$set(this, 'comments', [])
            var val = lm_idx==this.accordion?-1:lm_idx
            this.$set(this, 'accordion', val) //  
            this.getComments('modules', lm_idx)
        }, // accordionOpen()



        // ==========  /  /  ========== //







        // ==========  ========== //

        // 
        plusScore(table, id, score){
            /*
            table   :    ( ex- group, student )
            id        :    
            score  :   
            */
            if ( !confirm(score+' ?') ) {
                return
            }

            var url = '/api/students/score'
            this.$http.put(url, { table, id, score })
            .then(resp => {

                switch (table) {
                    case 'group':
                        //  
                        var rid = this.groups.findIndex(group=>{
                            return group.group_idx == id
                        })
                        //
                        this.$set(this.groups[rid], 'group_score', this.groups[rid].group_score+score)
                        break;

                    case 'registration':
                        //  
                        var rid = this.students.findIndex(std=>{
                            return std.stu_idx == id
                        })
                        //
                        this.$set(this.students[rid], 'stu_score', this.students[rid].stu_score+score)
                        break;
                }// switch

            })
            .catch(err => {
                console.log(err);
                alert('error')
            })
        },// 



        //   
        getActionPlan( stu_idx ){
            this.$set(this, 'selectedStudent', stu_idx)
            var url = '/api/plans/'+this.lec_idx+'/'+stu_idx
            this.$http.get(url)
            .then(resp=>{
                this.$set(this, 'plans', resp.data.plans)
            })
            .catch(err=>{
                alert('error')
            })
        },


        //  
        selectPlan( id ) {
            this.$http.get('/api/plans/detail/'+id)
            .then(resp=>{
                console.log(resp.data);
                this.$set(this, 'plan', resp.data.plan)
                this.$set(this.modal, 'actionPlan', true)
            })
            .catch(err=>{
                alert('Error - ')
            })
        },//  


        // ==========  ========== //






    }// methods
}
</script>




<style scoped>
    h1, h2 {
      font-weight: normal;
    }
    ul {
      list-style-type: none;
      padding: 0;
    }
    li {
      display: inline-block;
      margin: 0 10px;
    }
    a {
      color: #42b983;
    }

    .segment{
        padding:20px;
    }


    .lectureInfo a:hover{color:#c1c1c1;}
    .lectureInfo a{color:#333; text-decoration: underline; cursor: pointer;}


    th{
        background: #F9FAFB;
        padding:0.92857143em 0.78571429em;
    }
    /*.borderBottom{
        border-bottom: 1px solid rgba(34, 36, 38, 0.1);
    }
    .borderTop{
        border-top: 1px solid rgba(34, 36, 38, 0.1);
    }*/


    .singleLineTd{
        border-right:none !important;
        border-left:none !important;
    }


    .tempRow{ cursor: pointer; }
    .tempRow:hover{ background: #f9f9f9 !important; }



        .lectureMenu .active{
            color: #fff !important;
            background: #3b485f !important;
            border-radius: 0 !important;
        }

        .tab.segment{
            padding:30px 25px !important;
        }



        .list > .item {
            margin:3px;
            padding-top: 10px;
            padding-bottom: 5px;
        }



        h3 .icon {
            color:rgba(0, 0, 0, 0.45) !important;
            font-size: 0.95em !important;
        }



        .moduleLabel{
            padding:0 !important;
            width:57px !important;
        }
        .moduleContent{
            padding:0 !important;
            width:calc(90% - 60px) !important;
        }


        .fixButton{
            position: absolute;
            top: 0;
            right:0;
            z-index: 99;
        }


        .actionPlan td{
            padding:20px 18px;
            cursor: pointer;
        }


        .actionPlanUserList .item{
            cursor: pointer;
        }
        .actionPlanUserList .item:hover{
            background: #f1f1f1;
        }
</style>



