<template lang="html">
    <div class="fields" style="padding-top:13px;">
        <div class="eight wide field" style="padding-right:3px;">
            <select class="ui fluid search dropdown timepicker" @change="hChange" v-model="valHour" >
                <option value=""></option>
                <option v-for="h in hh" v-bind:value="h" >{{h<10?'0':''}}{{h}}</option>
            </select>
        </div>
        <div class="eight wide field" style="padding-left:3px;">
            <select class="ui fluid search dropdown timepicker" @change="hChange" v-model="valMinute" >
                <option value=""></option>
                <option v-for="(m) in mm" v-bind:value="m" >{{m<10?'0':''}}{{m}}</option>
            </select>
        </div>
    </div>
</template>

<script>
/*
Description>
1.     Props(defaultHour, defaultMinute)  
2.   flag     -   
3. start*, end*    /  
*/
export default {
    name: 'timepicker',

    props: {
        defaultTime:{
            default () { return '' }
        },

        starth:{ //  - 
            type: [String,Number],
            default() { return 1 }
        },
        endh:{ //  - 
            type: [String,Number],
            default() { return 24 }
        },
        startm:{ //  - 
            type: [String,Number],
            default() { return 0 }
        },
        endm:{ //  - 
            type: [String,Number],
            default() { return 59 }
        },

        flag:{
            type:String,
            default() { return 'timepicker'}
        }  // ,    (  )
    },

    data(){
        return{
            valHour : '',
            valMinute : '',
            hh   : [],
            mm : []
        }
    }, // data

    created(){
        // console.log("this.defaultTime : ", this.defaultTime);
        var aa = new Date(Date.parse(this.defaultTime))
        console.log("TIME: ", Date.parse(this.defaultTime));
        this.rangeSetting()
        this.defaultSetting()
        this.hChange()
    }, // created

    updated(){

        this.defaultSetting()
    },


    methods:{

        defaultSetting(){
            if (this.defaultTime) {
                var tt = new Date()
                this.$set(this, 'valHour', tt.getHours())
                this.$set(this, 'valMinute', tt.getMinutes())
            }
        },//

        rangeSetting(){
            for(var i=this.starth;  i<=this.endh;  i++ ){
                this.hh.push(i)
            }
            for(var j=this.startm;  j<=this.endm;  j++ ){
                this.mm.push(j)
            }
        },

        hChange(){

            var test = new Date()
            test.setHours(this.valHour)
            test.setMinutes(this.valMinute)

            console.log(test);

            //        
            this.$EventBus.$emit(this.flag, test)
        }

    }// methods

}
</script>

<style lang="css">
</style>



