<template lang="html">
    <div class="fields" style="padding-top:13px;">
        <div class="eight wide field" style="padding-right:3px;">
            <select class="ui fluid search dropdown timepicker" @change="hChange" v-model="valHour" >
                <option value=""></option>
                <option v-for="h in hh" v-bind:value="h" >{{h<10?'0':''}}{{h}}</option>
            </select>
        </div>
        <div class="eight wide field" style="padding-left:3px;">
            <select class="ui fluid search dropdown timepicker" @change="hChange" v-model="valMinute" >
                <option value=""></option>
                <option v-for="(m) in mm" v-bind:value="m" >{{m<10?'0':''}}{{m}}</option>
            </select>
        </div>
    </div>
</template>

<script>
/*
Description>
1.     Props(defaultHour, defaultMinute)  
2.   flag     -   
3. start*, end*    /  
*/
export default {
    name: 'timepicker',

    props: {
        defaultTime : { //  - 
            type: [String,Number],
            default() { return '' }
        },

        defaultHour:{ //  - 
            type: [String,Number],
            default() { return '' }
        },
        defaultMinute:{ //  - 
            type: [String,Number],
            default() { return '' }
        },

        starth:{ //  - 
            type: [String,Number],
            default() { return 1 }
        },
        endh:{ //  - 
            type: [String,Number],
            default() { return 24 }
        },
        startm:{ //  - 
            type: [String,Number],
            default() { return 0 }
        },
        endm:{ //  - 
            type: [String,Number],
            default() { return 59 }
        },

        flag:{
            type:String,
            default() { return 'timepicker'}
        }  // ,    (  )
    },

    data(){
        return{
            valHour : '',
            valMinute : '',
            hh   : [],
            mm : []
        }
    }, // data

    created(){
        // console.log("this.defaultHour : ", this.defaultHour);
        this.rangeSetting()
        this.defaultSetting()
        this.hChange()
    }, // created

    updated(){
        // console.log("this.flag : ", this.flag);
        this.defaultSetting()
    },




    methods:{

        defaultSetting(){
            // if (this.defaultHour) {
            //     this.$set(this, 'valHour', this.defaultHour)
            //     this.$set(this, 'valMinute', this.defaultMinute)
            // }
            // console.log("this.defaultTime : ", this.defaultTime);
            // console.log('deffffff');
            if (this.defaultTime && this.defaultTime!='') {

                var d = this.defaultTime.split(":")
                this.$set(this, 'valHour', Number(d[0]?d[0]:0))
                this.$set(this, 'valMinute', Number(d[1]?d[1]:0))
            }
        },//

        rangeSetting(){
            var sh = this.starth // 
            var sm = this.startm//  
            var eh = this.endh // 
            var em = this.endm //  

            //    
            if ( typeof(sh)=="string" ) {
                var tem = this.starth.split(":")
                sh = Number(tem[0])
                // sm = Number(tem[1])
            }
            if ( typeof(eh)=="string" ) {
                var tem = this.endh.split(":")
                eh = Number(tem[0])
                // em = Number(tem[1])
            }

            // console.log(this.flag+": "+sh);

            // 
            this.$set(this, 'valHour', sh)
            this.$set(this, 'valMinute', sm)


            for(var i=sh;  i<=eh;  i++ ){
                this.hh.push(i)
            }
            for(var j=sm;  j<=em;  j++ ){
                this.mm.push(j)
            }
        },

        hChange(){
            var timepicker = {
                h: this.valHour,
                m: this.valMinute
            }
            if (this.flag) {
                timepicker.flag= this.flag
            }
            //        
            // console.log("this.valMinute : ", String((this.valHour?this.valHour:"00")+":"+(this.valMinute?this.valMinute:"00")+":00"));
            // console.log("this.valMinute : ", this.valMinute);
            this.$EventBus.$emit(this.flag, String((this.valHour?this.valHour:"00")+":"+(this.valMinute?this.valMinute:"00")+":00"))
        }

    }// methods

}
</script>

<style lang="css">
</style>



